import {Component, OnDestroy, OnInit} from '@angular/core';
import {SharedServiceService} from '../../shared-service.service';
import {Router} from '@angular/router';
import {ApiService} from '../../api.service';
import {AlertController} from '@ionic/angular';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage {
  handlerMessage = '';
  userDetails: any = {};

  constructor(private apiService: ApiService, private alertController: AlertController, public router: Router, private sharedService: SharedServiceService) { }

  ionViewWillEnter() {
    this.sharedService.onSettingEvent.next(true);
    this.sharedService.showBackIcon.next(true);
    this.apiService.userSubject.subscribe((data) => {
      this.userDetails = data;
    });
  }

  // when Application will leave the page
  ionViewWillLeave() {
    this.sharedService.onSettingEvent.next(false);
    this.sharedService.showBackIcon.next(false);
  }

  onLogout() {
    this.apiService.logout();
  }

  async presentAlert() {
    const alert = await this.alertController.create({
      header: 'Do you want to Logout',
      cssClass: 'alert-popup',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            this.handlerMessage = 'Alert canceled';
          },
        },
        {
          text: 'OK',
          role: 'confirm',
          handler: () => {
            this.sharedService.removeAllUserDetails();
            this.onLogout();
            this.handlerMessage = 'Alert confirmed';
          },
        },
      ],
    });
    await alert.present();
  }



}
